import React,{useState} from 'react'
import { connect } from 'react-redux';
import './CartCard.css';
import {AdjustItemQty,removeFromCart} from "../../../Redux/ShoppingCart/Actions";
const CartCard = ({item,adjustQty,removeFromCart}) => {
    const [input, setInput] = useState(item.qty);

    const onChangeHandler = (e) => {
      setInput(e.target.value);
      adjustQty(item.id, e.target.value);
    };
    return (
        <div className="Container1">
            <div className="CartBox shadow-5">
            <img alt="Product " src={item.image} className="ProductImage"/>
            <p className="ProductName">{item.title}</p>
            <p className="ProductName">{item.price}$</p>
            <div className="QTYBOX">
                <p>QTY</p>
                <input type="number" min="1" max="99" className="QTYINPUT" placeholder="QTY" value={input}  onChange={onChangeHandler}/>
            </div>
            <button className="PreviewBtn" onClick={()=>removeFromCart(item)}>Delete</button>
            </div>
        </div>
    )
}
const mapDispatchToProps = (dispatch) => {
    return {
        adjustQty: (id, value) => dispatch(AdjustItemQty(id, value)),
        removeFromCart: (id) => dispatch(removeFromCart(id)),    };
  };
   

  
export default connect(null,mapDispatchToProps)(CartCard);
