import React, { Component } from 'react';
import { customHistory } from "../../App";
import "./Register.css";
const patterns={
    email:"[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$",// eslint-disable-line
    password:".{8,}",// eslint-disable-line
    name:"[A-Za-z]{1,32}",
    phone:"[0-9]{4}[0-9]{3}[0-9]{4}"
}
class Register extends Component{
    render()
    {
    return (
        <div className="Box">
        <div className="RegistrationForm shadow-5"> 
        <p className="RegistrationLabel">Register</p>
       <p className="Label">E-mail <input type="text" placeholder="Name@Example.com" maxlength="80" pattern={patterns.email} className="textInput" required/></p>
       <p className="Label">FullName <input type="text" placeholder="Your FullName" maxlength="60" pattern={patterns.name} className="textInput" required/></p>
       <p className="Label">Phone<input type="text" placeholder="Your Phone Number" pattern={patterns.phone} maxlength="11"  className="textInput" required/></p>
       <p className="RegisterPassword">Password <input type="password"  maxlength="40" pattern={patterns.password} placeholder="Your Password"  className="PasswordInput" required/> </p>
       <button className="RegisterBtn shadow-5">Register</button>
       <p className="GoSignInLabel">already have an account? <span className="GoSignIn" onClick={()=>customHistory.push('/Login')}>SignIn</span></p> 
        </div>
        </div>
    )
    }
}

export default Register;
