import {Router, Route, Switch  } from "react-router-dom";
import './App.css';
import AddressNav from './Components/AddressNav/AddressNav.js';
import SearchNav from './Components/SearchNav/SearchNav.js';
import FilterNav from './Components/FilterNav/FilterNav.js';
import Login from './Components/Login/Login.js';
import Cart from './Components/Cart/Cart.js';
import NotFound from './Components/NotFound/NotFound.js';
import ProductPreview from './Components/ProductPreview/ProductPreview.js';
import './Components/FontAwesomeIcons';
import Register from './Components/Register/Register.js';
import {createBrowserHistory} from 'history';
import Products from './Components/ProductCard/Products.js';

export const customHistory = createBrowserHistory();

const  App =()=> {
  return (
    <div className="App">
     <AddressNav/>
      <SearchNav/>
      <FilterNav/>
      <Router  history={customHistory}>
      <Switch>
          <Route exact path='/' component={Products}/>
          <Route exact path='/login' component={Login}/>
          <Route exact path='/Register' component={Register}/>
          <Route exact path='/cart' component={Cart}/>
          <Route exact path="/product/:id" component={ProductPreview} />
          <Route  component={NotFound}/>


      </Switch>
      </Router>
     
    </div>
  );
}
export default App;
