import React, { useState, useEffect } from 'react'
import './SearchNav.css';
import { customHistory } from "../../App";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import { connect } from 'react-redux';
const SearchNav = ({Cart}) => {
    const [cartCount, setCartCount] = useState(0);

  useEffect(() => {
    let count = 0;
    Cart.forEach((item) => {
      count += item.qty;
    });

    setCartCount(count);
  }, [Cart, cartCount]);

    return (
        
        <div className="searchNavigation">
        <div className="logoResize">
        <div className="Logo" onClick={()=>customHistory.push('/')}>
        Clotho.
        </div>
         </div>
         <div className="SearchBar">
        <input type="text" className="SearchInput" pattern="[^'\x22]+"/>
        <button className="SearchBtn">Search</button>
        </div>
        <div className="shoppingCart dim" onClick={()=>customHistory.push('/cart')}>
        <FontAwesomeIcon icon='shopping-cart'/> {cartCount}<br/>
        Cart
        </div>
       <div className="Login dim" onClick={()=>customHistory.push('/login')}>
        <FontAwesomeIcon icon='user'/><br/>
        Login
        </div>
         </div>
      
    )
}

const mapStateToProps = (state) => {
    return {
      Cart: state.ShoppigCart.cart,
    };
  };
  
export default connect(mapStateToProps)(SearchNav);
