import React, { Component } from 'react';
import { customHistory } from "../../App";
import "./Login.css";
const patterns={
    email:"[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$",// eslint-disable-line
    password:"(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"// eslint-disable-line
}
class Login extends Component{
    render()
    {
    return (
        <div className="Box">
        <div className="LoginForm shadow-5"> 
        <p className="LoginLabel">Login</p>
       <p className="EmailLabel">E-mail <input type="text" placeholder="E-mail" pattern={patterns.email}  className="EmailInput" required/></p>
       <p className="PasswordLabel">Password <input type="password" placeholder="Password" pattern={patterns.password}  className="PasswordInput" required/> </p>
       <button className="LoginBtn shadow-5">Login</button>
       <p className="GoRegisterLabel">New to Clotho? <span className="GoRegisterBtn" onClick={()=>customHistory.push('/Register')}>Create Account</span></p> 
        </div>
        </div>
    )
    }
}

export default Login;
