import React from 'react';
import './ProductPreview.css';
import { connect } from 'react-redux';
import { AddToCart } from "../../Redux/ShoppingCart/Actions";

const ProductPreview = ({current,addToCart}) => {
    return (
        <div>
            <div className="preview-box shadow-5">
                <img alt="Product" className="ProductImagePreview" src={current.image}/>
                <div className="details">
                <p className="productname">{current.title}</p>
                <p className="discription">Discription</p>
                <p className="discriptiontext">For protein shakes, pre-workout drinks, post-workout drinks or weight-related supplements, you need a good shaker bottle that allows you to mix the supplements well without leaving any lumps.</p>
                <p className="Price">Price : {current.price}$</p>
                <button className="AddtoCartBtn"  onClick={() => addToCart(current)}>Add to Cart</button>
                </div>
            </div>
        </div>
    )
}
const mapStateToProps = (state) => {
    return {
      current: state.ShoppigCart.currentItem,
    };
  };
  const mapDispatchToProps = (dispatch) => {
    return {
      addToCart: (id) => dispatch(AddToCart(id)),
    };
  };
export default connect(mapStateToProps,mapDispatchToProps)(ProductPreview)
