import React from 'react'
import './NotFound.css';
import { customHistory } from "../../App";

const NotFound = () => {
    return (
        <div className="NotFound">
          <span className="fourOfour">404</span> <span  className="textNotFound">Page Not Found</span>
          <span className="GoHome" onClick={()=>customHistory.push('/')}>Go To Home</span>   
        </div>
    )
}

export default NotFound
