import React from 'react';
import './ProductCard.css'
import { Link } from "react-router-dom";
import { connect } from 'react-redux';
import { loadCurrentItem } from "../../Redux/ShoppingCart/Actions";

const ProductCard =({product, loadCurrentItem})=>{
        return(
            <div className="container1">
	        <div className="grid">
            <div className="Card shadow-5">
            <img alt="Product " src={product.image} className="ProductImage"/>
            <p className="ProductName">{product.title}</p>
            <p className="ProductName">{product.price}$</p>
            <p className="ProductName">Rating</p>
            <Link to={`/product/${product.id}`}>
            <button className="PreviewBtn" onClick={() => loadCurrentItem(product)}>Preview</button>
            </Link>
            </div>
            </div>
            </div>
        )
}
const mapDispatchToProps = (dispatch) => {
    return {
    loadCurrentItem: item => dispatch(loadCurrentItem(item))
    };
  };
export default connect(null,mapDispatchToProps)( ProductCard);